import { Button, makeStyles, Typography } from "@material-ui/core";
import { getTokenFromAddress } from "config/networks";
import { useConnectedWeb3Context } from "contexts";
import { BigNumber } from "ethers";
import { transparentize } from "polished";
import React, { useEffect, useState } from "react";
import { IToken } from "types";
import { formatBigNumber } from "utils";
import { ZERO } from "utils/number";
import { NULL_ADDRESS } from "config/constants";
import { useIsMountedRef, useServices } from "helpers";
import idoRefundAbi from "abis/idoRefund.json";
import { IDORefundService } from "services/idoRefund";
import { useSnackbar } from "notistack";

const useStyles = makeStyles((theme) => ({
  root: {
    padding: 24,
    border: `1px solid ${transparentize(0.8, theme.colors.neutralLighter)}`,
    backgroundImage: "url(/assets/bg-1.png)",
    backgroundPosition: "top",

    marginTop: 24,
  },
  header: { paddingBottom: 12 },
  label: {
    color: theme.colors.neutralLighter,
    fontWeight: 500,
    "& span": {
      color: theme.colors.primary,
    },
    "& + &": {
      marginTop: 8,
    },
  },
  balanceLabel: {
    color: theme.colors.gray2,
    fontSize: 14,
    marginTop: 8,
  },
  headerLabel: {
    color: theme.colors.neutralLighter,
    fontSize: 24,
    fontWeight: 600,
  },
  footer: { paddingTop: 16 },
  button: {
    height: 40,

    "&:disabled": {
      backgroundColor: `${transparentize(0.4, theme.colors.gray2)} !important`,
      "& span": {
        color: `${transparentize(0.4, theme.colors.neutralLighter)} !important`,
      },
    },
  },
}));

interface IProps {
  address: string;
}

interface IState {
  amount: BigNumber;
  refunded: boolean;
  fundToken: string;
}

export const RefundSection = (props: IProps) => {
  const { address } = props;
  const classes = useStyles();
  const {
    account,
    library: provider,
    networkId,
    setTxModalInfo,
  } = useConnectedWeb3Context();
  const [state, setState] = useState<IState>({
    amount: ZERO,
    refunded: false,
    fundToken: NULL_ADDRESS,
  });
  const { multicall } = useServices();
  const isMountedRef = useIsMountedRef();
  const { enqueueSnackbar } = useSnackbar();

  const loadInfo = async () => {
    if (!account || !provider) {
      setState((prev) => ({
        ...prev,
        amount: ZERO,
        refunded: false,
        fundToken: NULL_ADDRESS,
      }));
      return;
    }
    try {
      const infoPools = [
        { name: "fundToken", address: address, params: [] },
        { name: "getTotalFunded", address: address, params: [account] },
        { name: "refunded", address: address, params: [account] },
      ];
      const [[fundToken], [totalFunded], [refunded]] =
        await multicall.multicallv2(idoRefundAbi, infoPools, {
          requireSuccess: false,
        });
      if (isMountedRef.current === true)
        setState((prev) => ({
          ...prev,
          amount: totalFunded,
          refunded,
          fundToken,
        }));
    } catch (error) {
      if (isMountedRef.current === true)
        setState((prev) => ({
          ...prev,
          amount: ZERO,
          refunded: false,
          fundToken: NULL_ADDRESS,
        }));
    }
  };

  const onRefund = async () => {
    if (!account || !provider) {
      return;
    }
    try {
      setTxModalInfo(true, "Refunding...");
      const refundService = new IDORefundService(provider, account, address);
      const txId = await refundService.refund();
      setTxModalInfo(
        true,
        "Refunding...",
        "Please wait until tx is confirmed",
        txId
      );
      await provider.waitForTransaction(txId);
      enqueueSnackbar("Your fund is refuned successfully");
      setTxModalInfo(true, "Reloading...");
      await loadInfo();
      setTxModalInfo(false);
    } catch (error) {
      setTxModalInfo(false);
      enqueueSnackbar(error.message || "Something went wrong!", {
        variant: "error",
      });
    }
  };

  useEffect(() => {
    loadInfo();
  }, [account, props.address, networkId]);

  // if (state.amount.isZero()) return null;
  let fundToken: IToken;

  try {
    fundToken = getTokenFromAddress(state.fundToken, networkId);
  } catch (error) {
    return null;
  }

  const renderContent = () => {
    return (
      <>
        <div>
          <Typography className={classes.label}>
            Funded Amount:{" "}
            <span>
              {formatBigNumber(state.amount, fundToken.decimals)}{" "}
              {fundToken.symbol.toUpperCase()}
            </span>
          </Typography>
          {state.refunded && (
            <Typography className={classes.label}>Already refunded</Typography>
          )}
        </div>
        <div className={classes.footer}>
          <div>
            <Button
              className={classes.button}
              color="primary"
              fullWidth
              variant="contained"
              disabled={state.refunded || state.amount.isZero()}
              onClick={onRefund}
            >
              Refund
            </Button>
          </div>
        </div>
      </>
    );
  };

  return (
    <div className={classes.root}>
      <div className={classes.header}>
        <Typography className={classes.headerLabel}>Refund</Typography>
      </div>
      {renderContent()}
    </div>
  );
};
