import { Button, makeStyles, Typography } from "@material-ui/core";
import { BigNumberInput, CountDownTimer } from "components";
import { getTokenFromAddress } from "config/networks";
import { useConnectedWeb3Context } from "contexts";
import { BigNumber } from "ethers";
import { useTokenBalance } from "helpers";
import { transparentize } from "polished";
import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import { IIDO, IMetadata, IToken } from "types";
import {
  formatBigNumber,
  hideInsignificantZeros,
  numberWithCommas,
} from "utils";
import { IDOStatus, IDOVersion } from "utils/enums";
import { getCurrentTimeStamp, getIDOStatus } from "utils/ido";
import { ZERO } from "utils/number";
import moment from "moment-timezone";
import { parseUnits } from "ethers/lib/utils";
import {
  CLAIM_LINKS,
  ETHER_DECIMAL,
  NETWORK_NAMES,
  NULL_ADDRESS,
} from "config/constants";

const useStyles = makeStyles((theme) => ({
  root: {
    padding: 16,
    backgroundPosition: "top",
    backgroundColor: theme.colors.neutralLight,
  },
  header: { padding: 12 },
  label: {
    color: theme.colors.neutralLighter,
    fontWeight: 300,
    "& span": {
      fontWeight: 800,
    },
    "& + &": {
      marginTop: 8,
    },
  },
  balanceLabel: {
    color: theme.colors.gray2,
    fontSize: 14,
    marginTop: 8,
  },
  headerLabel: { color: theme.colors.gray2, fontSize: 14 },
  headerTimer: {
    fontSize: 24,
    fontWeight: 600,
  },
  footer: { paddingTop: 16 },
  button: {
    height: 40,

    "&:disabled": {
      backgroundColor: `${transparentize(0.4, theme.colors.gray2)} !important`,
      "& span": {
        color: `${transparentize(0.4, theme.colors.neutralLighter)} !important`,
      },
    },
  },
  linkButton: {
    height: 40,

    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    textDecoration: "none",
    backgroundColor: theme.colors.primary,
    color: theme.colors.black,
    fontWeight: 600,
  },
}));

interface IProps {
  data: IIDO & { meta: Required<IMetadata> };
  onBuy: (_: BigNumber) => Promise<void>;
  onClaim: () => Promise<void>;
  personalRoundMaxAllocation: BigNumber;
  personalRoundFunded: BigNumber;
  personalReleasableAmount: BigNumber;
  personalReleased: BigNumber;
  currentRound: BigNumber;
  tierWhitelisted: boolean;
  whitelistEnabled: boolean;
  tierId: number;
  roundsTotalAllocation: { [key: string]: BigNumber };
}

interface IState {
  amount: BigNumber;
}

export const ActionSection = (props: IProps) => {
  const {
    data,
    onBuy,
    personalRoundMaxAllocation,
    personalRoundFunded,
    personalReleasableAmount,
    onClaim,
    tierWhitelisted,
    whitelistEnabled,
    currentRound,
    tierId,
    roundsTotalAllocation,
    personalReleased,
  } = props;
  const classes = useStyles();
  const { startTime, endTime } = data;
  const { networkId } = useConnectedWeb3Context();
  const [counter, setCounter] = useState(1);
  const increateCounter = () => setCounter((counter) => counter + 1);
  const [state, setState] = useState<IState>({ amount: ZERO });
  const { balance: fundTokenBalance } = useTokenBalance(data.fundToken);

  const history = useHistory();

  let fundToken: IToken | null = null;

  try {
    fundToken = getTokenFromAddress(data.fundToken, networkId);
  } catch (error) {
    history.push("/idos");
  }
  const status = getIDOStatus(data);

  const hasWhitelistFunctionality = ["v3", "v4"].includes(data.version);

  const isDisabled = (() => {
    if (data.version === IDOVersion.sec) {
      if (
        state.amount.isZero() ||
        personalRoundFunded.add(state.amount).lt(data.minFundAmount) ||
        personalRoundFunded.add(state.amount).gt(personalRoundMaxAllocation) ||
        state.amount.gt(fundTokenBalance)
      ) {
        return true;
      }

      const roundInfo = data.rounds.find((round) =>
        round.roundId.eq(currentRound)
      );
      const currentRoundFundRaised = (roundInfo || {}).fundRaised || ZERO;

      if (
        currentRoundFundRaised
          .add(state.amount)
          .gt(roundsTotalAllocation[currentRound.toString()])
      ) {
        return true;
      }

      return false;
    }

    if (
      state.amount.isZero() ||
      personalRoundFunded.add(state.amount).lt(data.minFundAmount) ||
      personalRoundFunded.add(state.amount).gt(personalRoundMaxAllocation) ||
      state.amount.gt(fundTokenBalance) ||
      (hasWhitelistFunctionality &&
        whitelistEnabled &&
        !tierWhitelisted &&
        currentRound.toNumber() <= 4)
    ) {
      return true;
    }

    if (currentRound.toNumber() <= 4 && tierId <= 4) {
      if (tierId === 0 || currentRound.isZero()) {
        return true;
      }
      const roundInfo = data.rounds.find(
        (round) => round.roundId.toNumber() === 5 - tierId
      );
      const currentRoundFundRaised = (roundInfo || {}).fundRaised || ZERO;

      if (
        currentRoundFundRaised
          .add(state.amount)
          .gt(roundsTotalAllocation[5 - tierId])
      ) {
        return true;
      }
    } else {
      const roundInfo = data.rounds.find((round) =>
        round.roundId.eq(currentRound)
      );
      const currentRoundFundRaised = (roundInfo || {}).fundRaised || ZERO;

      if (
        currentRoundFundRaised
          .add(state.amount)
          .gt(roundsTotalAllocation[currentRound.toString()])
      ) {
        return true;
      }
    }

    return false;
  })();

  const maxInputValue = (() => {
    let maxInput = personalRoundMaxAllocation.sub(personalRoundFunded);
    if (maxInput.gt(fundTokenBalance)) {
      maxInput = fundTokenBalance;
    }

    if (
      data.version !== IDOVersion.sec &&
      currentRound.toNumber() <= 4 &&
      tierId <= 4
    ) {
      if (tierId === 0 || currentRound.isZero()) {
        return ZERO;
      }
      const roundInfo = data.rounds.find(
        (round) => round.roundId.toNumber() === 5 - tierId
      );
      const currentRoundFundRaised = (roundInfo || {}).fundRaised || ZERO;

      const remain = roundsTotalAllocation[5 - tierId].sub(
        currentRoundFundRaised
      );

      if (maxInput.gt(remain)) {
        maxInput = remain;
      }
    } else {
      if (currentRound.isZero()) {
        return ZERO;
      }

      const roundInfo = data.rounds.find((round) =>
        round.roundId.eq(currentRound)
      );
      const currentRoundFundRaised = (roundInfo || {}).fundRaised || ZERO;
      const remain = roundsTotalAllocation[currentRound.toString()].sub(
        currentRoundFundRaised
      );

      if (maxInput.gt(remain)) {
        maxInput = remain;
      }
    }

    if (maxInput.lt(ZERO)) {
      maxInput = ZERO;
    }

    return maxInput;
  })();

  if (!fundToken) return null;

  const showNextVestingInfo = () => {
    if (data.distributePercentAtClaim === 1000) {
      return null;
    }
    const lastTime =
      data.cliffTime + data.vestingDuration - data.vestingPeriodicity;
    const currentTimeStamp = getCurrentTimeStamp();

    if (lastTime < currentTimeStamp) {
      return null;
    }

    let nextClaimTime = data.cliffTime;

    if (currentTimeStamp > data.cliffTime) {
      nextClaimTime =
        data.cliffTime +
        Math.ceil(
          (currentTimeStamp - data.cliffTime) / data.vestingPeriodicity
        ) *
          data.vestingPeriodicity;
    }

    return (
      <div>
        <Typography className={classes.headerLabel}>
          Until Next Unlock:
        </Typography>
        <CountDownTimer
          destTimeStamp={nextClaimTime * 1000}
          onReached={increateCounter}
          className={classes.headerTimer}
        />
      </div>
    );
  };

  return (
    <div className={classes.root}>
      <div className={classes.header}>
        {status === IDOStatus.Upcoming && (
          <div>
            <Typography className={classes.headerLabel}>Start Time:</Typography>
            <CountDownTimer
              destTimeStamp={startTime * 1000}
              onReached={increateCounter}
              className={classes.headerTimer}
            />
          </div>
        )}
        {status === IDOStatus.Ongoing && (
          <div>
            <Typography className={classes.headerLabel}>End Time:</Typography>
            <CountDownTimer
              destTimeStamp={endTime * 1000}
              onReached={increateCounter}
              className={classes.headerTimer}
            />
          </div>
        )}
        {status === IDOStatus.Ended && (
          <div>
            {data.saleToken === NULL_ADDRESS ? (
              <Typography className={classes.headerLabel}>
                Tokens will be sent automatically 15 mins after DEX listing.
              </Typography>
            ) : (
              <CountDownTimer
                destTimeStamp={data.claimTime * 1000}
                onReached={increateCounter}
                className={classes.headerTimer}
              />
            )}
          </div>
        )}
        {status === IDOStatus.Claimable && showNextVestingInfo()}
      </div>
      <div>
        <Typography className={classes.label}>
          Start Time:{" "}
          <span>
            {moment(new Date(startTime * 1000))
              .utc()
              .format("MM/DD/YYYY HH:mm")}{" "}
            GMT
          </span>
        </Typography>
        <Typography className={classes.label}>
          End Time:{" "}
          <span>
            {moment(new Date(endTime * 1000))
              .utc()
              .format("MM/DD/YYYY HH:mm")}{" "}
            GMT
          </span>
        </Typography>
        <Typography className={classes.label}>
          Symbol: <span>{data.meta.tokenSymbol}</span>
        </Typography>
        <Typography className={classes.label}>
          Blockchain Network:{" "}
          <span>{NETWORK_NAMES[data.address] || "BSC"}</span>
        </Typography>
        <Typography className={classes.label}>
          Total Sale Token:{" "}
          <span>
            {numberWithCommas(
              formatBigNumber(data.saleTarget, data.meta.tokenDecimals, 0)
            )}{" "}
            {data.meta.tokenSymbol}
          </span>
        </Typography>
        <Typography className={classes.label}>
          Total Fund Token:{" "}
          <span>
            {numberWithCommas(
              formatBigNumber(data.fundTarget, fundToken.decimals, 0)
            )}{" "}
            {fundToken.symbol.toUpperCase()}
          </span>
        </Typography>
        <Typography className={classes.label}>
          Min Fund Token:{" "}
          <span>
            {formatBigNumber(data.minFundAmount, fundToken.decimals, 0)}{" "}
            {fundToken.symbol.toUpperCase()}
          </span>
        </Typography>
        <Typography className={classes.label}>
          1 {fundToken.symbol.toUpperCase()} ={" "}
          <span>
            {numberWithCommas(
              hideInsignificantZeros(
                formatBigNumber(
                  data.saleTarget
                    .mul(
                      data.meta.tokenDecimals < ETHER_DECIMAL
                        ? parseUnits(
                            "1",
                            ETHER_DECIMAL - data.meta.tokenDecimals
                          )
                        : BigNumber.from(1)
                    )
                    .mul(1000000)
                    .div(
                      data.fundTarget.mul(
                        fundToken.decimals < ETHER_DECIMAL
                          ? parseUnits("1", ETHER_DECIMAL - fundToken.decimals)
                          : BigNumber.from(1)
                      )
                    ),
                  6,
                  6
                )
              )
            )}{" "}
            {data.meta.tokenSymbol}
          </span>
        </Typography>
        <Typography className={classes.label}>
          1 {data.meta.tokenSymbol} ={" "}
          <span>
            {numberWithCommas(
              hideInsignificantZeros(
                formatBigNumber(
                  data.fundTarget
                    .mul(
                      fundToken.decimals < ETHER_DECIMAL
                        ? parseUnits("1", ETHER_DECIMAL - fundToken.decimals)
                        : BigNumber.from(1)
                    )
                    .mul(1000000)
                    .div(
                      data.saleTarget.mul(
                        data.meta.tokenDecimals < ETHER_DECIMAL
                          ? parseUnits(
                              "1",
                              ETHER_DECIMAL - data.meta.tokenDecimals
                            )
                          : BigNumber.from(1)
                      )
                    ),
                  6,
                  6
                )
              )
            )}{" "}
            {fundToken.symbol.toUpperCase()}
          </span>
        </Typography>
        {status === IDOStatus.Claimable &&
          data.saleToken !== NULL_ADDRESS &&
          !CLAIM_LINKS[data.address] && (
            <>
              <Typography className={classes.label}>
                Total Claimed:{" "}
                <span>
                  {formatBigNumber(personalReleased, data.meta.tokenDecimals)}{" "}
                  {data.meta.tokenSymbol}
                </span>
              </Typography>
              <Typography className={classes.label}>
                Cliamable Amount:{" "}
                <span>
                  {formatBigNumber(
                    personalReleasableAmount,
                    data.meta.tokenDecimals
                  )}{" "}
                  {data.meta.tokenSymbol}
                </span>
              </Typography>
            </>
          )}
      </div>
      <div className={classes.footer}>
        {status === IDOStatus.Ongoing && (
          <div>
            <BigNumberInput
              value={state.amount}
              token={fundToken}
              onChange={(newValue) =>
                setState((prev) => ({ ...prev, amount: newValue }))
              }
              onMax={() => {
                setState((prev) => ({ ...prev, amount: maxInputValue }));
              }}
            />
            <Typography className={classes.balanceLabel}>
              {fundToken.symbol.toUpperCase()} Balance:{" "}
              {formatBigNumber(fundTokenBalance, fundToken.decimals)}
            </Typography>
            <br />
            <Button
              className={classes.button}
              color="primary"
              fullWidth
              variant="contained"
              onClick={() => onBuy(state.amount)}
              disabled={isDisabled}
            >
              Buy Token
            </Button>
          </div>
        )}
        {status === IDOStatus.Claimable &&
          (CLAIM_LINKS[data.address] ? (
            <>
              <a
                href={CLAIM_LINKS[data.address]}
                target="_blank"
                rel="noreferrer"
                className={classes.linkButton}
              >
                Claim Token
              </a>
            </>
          ) : data.saleToken !== NULL_ADDRESS ? (
            <div>
              <Typography className={classes.label}>
                You should keep your <span>TIER</span> to claim the tokens for
                the funds you raised during tier rounds
              </Typography>
              <br />
              <Button
                className={classes.button}
                color="primary"
                fullWidth
                variant="contained"
                onClick={onClaim}
                disabled={personalReleasableAmount.isZero()}
              >
                Claim Token
              </Button>
            </div>
          ) : (
            <div>
              <Typography className={classes.label}>
                Tokens are distributed at the same time
              </Typography>
            </div>
          ))}
      </div>
    </div>
  );
};
