import { Grid, makeStyles, Typography } from "@material-ui/core";
import { CountDownTimer, ProgressBar, TIERS } from "components";
import { getTokenFromAddress } from "config/networks";
import { useConnectedWeb3Context } from "contexts";
import { BigNumber } from "ethers";
import { transparentize } from "polished";
import React, { useState } from "react";
import { IIDO, IMetadata, IToken } from "types";
import { formatBigNumber, numberWithCommas } from "utils";
import { IDOStatus, IDORound, IDOVersion } from "utils/enums";
import { getCurrentTimeStamp, getIDOStatus, getTimeStr } from "utils/ido";
import moment from "moment-timezone";
import { ZERO } from "utils/number";
import { useHistory } from "react-router-dom";
import clsx from "clsx";

const useStyles = makeStyles((theme) => ({
  root: {
    padding: 16,
    backgroundPosition: "top",
    backgroundColor: theme.colors.neutralLight,
  },
  second: {
    borderTop: `1px solid ${transparentize(0.4, theme.colors.neutralLighter)}`,
    textAlign: "center",
    [theme.breakpoints.down("sm")]: {
      borderTop: `1px solid ${transparentize(
        0.4,
        theme.colors.neutralLighter
      )}`,
      borderLeft: "none",
    },
  },
  section: {
    padding: "0 12px",
  },
  totalSold: {
    color: theme.colors.neutralLighter,
    fontSize: 20,
    fontWeight: 600,
    "& span": {
      color: theme.colors.primary,
    },
    marginBottom: 12,
  },
  totalFundLabel: {
    color: theme.colors.gray2,
    fontSize: 14,
    marginTop: 8,
  },
  roundLabel: {
    color: theme.colors.neutralLighter,
    fontSize: 16,
    fontWeight: 600,
    "& span": {
      color: theme.colors.primary,
    },
    marginBottom: 4,
  },
  label: {
    color: theme.colors.neutralLighter,
    fontWeight: 300,
    "& span": {
      fontWeight: 800,
    },
    "&.error": {
      color: theme.colors.error,
    },
    "&.success": { color: theme.colors.sixth },
    "& + &": {
      marginTop: 8,
    },
  },
  roundProgress: {
    padding: "8px 0",
    borderTop: `1px dashed ${transparentize(0.9, theme.colors.neutralLighter)}`,
  },
  roundTimer: {
    justifyContent: "center",
  },
}));

interface IProps {
  data: IIDO & { meta: Required<IMetadata> };
  personalTotalFund: BigNumber;
  currentRound: BigNumber;
  roundsTotalAllocation: { [key: string]: BigNumber };
  personalRoundMaxAllocation: BigNumber;
  personalRoundFunded: BigNumber;
  tierWhitelisted: boolean;
  whitelistEnabled: boolean;
  tierId: number;
}

export const StatusSection = (props: IProps) => {
  const {
    data,
    personalRoundFunded,
    personalRoundMaxAllocation,
    personalTotalFund,
    currentRound,
    roundsTotalAllocation,
    tierWhitelisted,
    whitelistEnabled,
    tierId,
  } = props;
  const personalTotalSaleToken = personalTotalFund
    .mul(data.saleTarget)
    .div(data.fundTarget);
  const classes = useStyles();
  const { networkId } = useConnectedWeb3Context();
  const history = useHistory();

  let fundToken: IToken | null = null;

  try {
    fundToken = getTokenFromAddress(data.fundToken, networkId);
  } catch (error) {
    history.push("/idos");
  }

  const isV1 = data.version === "v1";
  const status = getIDOStatus(data);

  const percent =
    data.fundRaised.mul(10000).div(data.fundTarget).toNumber() / 100;

  const [counter, setCounter] = useState(1);
  const increateCounter = () => setCounter((counter) => counter + 1);
  const currentTimeStamp = getCurrentTimeStamp();

  const renderV2Progress = () => {
    if (!fundToken) return null;
    if (status === IDOStatus.Upcoming) return null;
    const prevHistory = [];

    if (data.version === IDOVersion.sec) {
      for (let id = 1; id <= 2; id++) {
        const roundInfo = data.rounds.find((round) => round.roundId.eq(id));
        const roundFundRaised = roundInfo ? roundInfo.fundRaised : ZERO;
        const roundTotalAllocation = roundsTotalAllocation[id] || ZERO;
        const percent = roundTotalAllocation.isZero()
          ? 0
          : roundFundRaised.mul(100).div(roundTotalAllocation).toNumber();

        const isCurrent = id === currentRound.toNumber();

        let roundStartTime = data.startTime;
        let roundEndTime = data.endTime;

        const roundPeriod = Math.floor((data.endTime - data.startTime) / 2);

        roundStartTime = data.startTime + roundPeriod * (id - 1);

        if (currentRound.toNumber() !== 2) {
          roundEndTime = data.startTime + roundPeriod * id;
        }

        prevHistory.push(
          <div key={`${id}`}>
            <div className={classes.roundProgress}>
              <Typography align="center" className={classes.roundLabel}>
                {(Object.values(IDORound) as any)[id + 4]}
              </Typography>
              <ProgressBar
                percent={percent}
                label={`Sold ${Math.floor(percent)}%`}
              />
              {currentTimeStamp > roundStartTime && (
                <Typography align="center" className={classes.totalFundLabel}>
                  {numberWithCommas(
                    formatBigNumber(roundFundRaised, fundToken.decimals)
                  )}{" "}
                  {fundToken.symbol.toUpperCase()} of{" "}
                  {numberWithCommas(
                    formatBigNumber(
                      roundsTotalAllocation[id] || ZERO,
                      fundToken.decimals
                    )
                  )}{" "}
                  {fundToken.symbol.toUpperCase()}
                </Typography>
              )}
              <Typography align="center" className={classes.totalFundLabel}>
                {moment(new Date(roundStartTime * 1000))
                  .utc()
                  .format("MM/DD/YYYY HH:mm")}{" "}
                GMT -{" "}
                {moment(new Date(roundEndTime * 1000))
                  .utc()
                  .format("MM/DD/YYYY HH:mm")}{" "}
                GMT
              </Typography>
            </div>
            {isCurrent && status === IDOStatus.Ongoing && (
              <>
                <CountDownTimer
                  className={classes.roundTimer}
                  destTimeStamp={roundEndTime * 1000}
                  onReached={increateCounter}
                />
                <br />
              </>
            )}
          </div>
        );
      }
    } else {
      for (let id = 1; id <= 6; id++) {
        const roundInfo = data.rounds.find((round) => round.roundId.eq(id));
        const roundFundRaised = roundInfo ? roundInfo.fundRaised : ZERO;
        const roundTotalAllocation = roundsTotalAllocation[id] || ZERO;
        const percent = roundTotalAllocation.isZero()
          ? 0
          : roundFundRaised.mul(100).div(roundTotalAllocation).toNumber();

        const isCurrent = id === currentRound.toNumber();

        let roundStartTime = data.startTime;
        let roundEndTime = data.endTime;

        const roundPeriod = Math.floor((data.endTime - data.startTime) / 6);

        if (data.version === "v4" && id <= 4) {
          roundEndTime = roundStartTime + roundPeriod * 4;
        } else {
          roundStartTime = data.startTime + roundPeriod * (id - 1);

          if (currentRound.toNumber() !== 6) {
            roundEndTime = data.startTime + roundPeriod * id;
          }
        }

        prevHistory.push(
          <div key={`${id}`}>
            <div className={classes.roundProgress}>
              <Typography align="center" className={classes.roundLabel}>
                {(Object.values(IDORound) as any)[id]}
              </Typography>
              <ProgressBar
                percent={percent}
                label={`Sold ${Math.floor(percent)}%`}
              />
              {currentTimeStamp > roundStartTime && (
                <Typography align="center" className={classes.totalFundLabel}>
                  {numberWithCommas(
                    formatBigNumber(roundFundRaised, fundToken.decimals)
                  )}{" "}
                  {fundToken.symbol.toUpperCase()} of{" "}
                  {numberWithCommas(
                    formatBigNumber(
                      roundsTotalAllocation[id] || ZERO,
                      fundToken.decimals
                    )
                  )}{" "}
                  {fundToken.symbol.toUpperCase()}
                </Typography>
              )}
              <Typography align="center" className={classes.totalFundLabel}>
                {moment(new Date(roundStartTime * 1000))
                  .utc()
                  .format("MM/DD/YYYY HH:mm")}{" "}
                GMT -{" "}
                {moment(new Date(roundEndTime * 1000))
                  .utc()
                  .format("MM/DD/YYYY HH:mm")}{" "}
                GMT
              </Typography>
            </div>
            {((id > 4 && isCurrent) || id <= 4) &&
              status === IDOStatus.Ongoing && (
                <>
                  <CountDownTimer
                    className={classes.roundTimer}
                    destTimeStamp={roundEndTime * 1000}
                    onReached={increateCounter}
                  />
                  <br />
                </>
              )}
          </div>
        );
      }
    }

    return (
      <>
        <br />
        {prevHistory}
      </>
    );
  };
  const renderV2 = () => {
    if (!fundToken) return null;
    if (status === IDOStatus.Upcoming || currentRound.toNumber() === 0)
      return null;

    const hasWhitelistFunctionality = ["v3", "v4"].includes(data.version);

    return (
      <>
        {hasWhitelistFunctionality &&
          whitelistEnabled &&
          currentRound.toNumber() <= 4 && (
            <Typography className={classes.label}>
              Only whitelisted tiers can purchase during tier round
            </Typography>
          )}
        {hasWhitelistFunctionality &&
          whitelistEnabled &&
          currentRound.toNumber() <= 4 && (
            <Typography
              className={clsx(
                classes.label,
                tierWhitelisted ? "success" : "error"
              )}
            >
              {tierWhitelisted
                ? "You're whitelisted"
                : "You're not whitelisted"}
            </Typography>
          )}
        <Typography className={classes.label}>
          Your max alloc:{" "}
          <span>
            {numberWithCommas(
              formatBigNumber(personalRoundMaxAllocation, fundToken.decimals, 2)
            )}{" "}
            {fundToken.symbol.toUpperCase()}
          </span>
        </Typography>
        {data.version !== IDOVersion.sec && (
          <Typography className={classes.label}>
            Your Tier:{" "}
            <span>{tierId > 0 ? TIERS[4 - tierId].name : "None"}</span>
          </Typography>
        )}
        <Typography className={classes.label}>
          Your{" "}
          {currentRound.toNumber() <= 4 && data.version !== IDOVersion.sec
            ? "tier rounds"
            : "round"}{" "}
          raised:{" "}
          <span>
            {numberWithCommas(
              formatBigNumber(personalRoundFunded, fundToken.decimals, 2)
            )}{" "}
            {fundToken.symbol.toUpperCase()}
          </span>
        </Typography>
      </>
    );
  };

  if (!fundToken) return null;

  return (
    <div className={classes.root}>
      <Grid container spacing={2}>
        <Grid item xs={12}>
          <div className={classes.section}>
            <Typography align="center" className={classes.totalSold}>
              Total Sold:{" "}
              <span>
                {numberWithCommas(
                  formatBigNumber(data.saleRaised, data.meta.tokenDecimals, 0)
                )}{" "}
                {data.meta.tokenSymbol}
              </span>
            </Typography>
            <ProgressBar
              percent={percent}
              thick
              label={`Total Sold ${Math.floor(percent)}%`}
            />
            <Typography align="center" className={classes.totalFundLabel}>
              {numberWithCommas(
                formatBigNumber(data.fundRaised, fundToken.decimals, 0)
              )}{" "}
              {fundToken.symbol.toUpperCase()} of{" "}
              {numberWithCommas(
                formatBigNumber(data.fundTarget, fundToken.decimals, 0)
              )}{" "}
              {fundToken.symbol.toUpperCase()}
            </Typography>
            {!isV1 && renderV2Progress()}
          </div>
        </Grid>
        <Grid item xs={12} className={classes.second}>
          <div className={classes.section}>
            <Typography className={classes.label}>
              Your total fund:{" "}
              <span>
                {numberWithCommas(
                  formatBigNumber(personalTotalFund, fundToken.decimals)
                )}{" "}
                {fundToken.symbol.toUpperCase()}
              </span>
            </Typography>
            <Typography className={classes.label}>
              Your total sale token:{" "}
              <span>
                {numberWithCommas(
                  formatBigNumber(
                    personalTotalSaleToken,
                    data.meta.tokenDecimals
                  )
                )}{" "}
                {data.meta.tokenSymbol}
              </span>
            </Typography>
            {data.cliffTime !== 0 && data.distributePercentAtClaim < 1000 && (
              <>
                <Typography className={classes.label}>
                  Cliff Time Start:{" "}
                  <span>
                    {moment(new Date(data.cliffTime * 1000))
                      .utc()
                      .format("MM/DD/YYYY HH:mm")}{" "}
                    GMT
                  </span>
                </Typography>
                <Typography className={classes.label}>
                  Vesting Duration:{" "}
                  <span>{getTimeStr(data.vestingDuration)}</span>
                </Typography>
                <Typography className={classes.label}>
                  Vesting Periodicity:{" "}
                  <span>{getTimeStr(data.vestingPeriodicity)}</span>
                </Typography>
              </>
            )}
            {!isV1 && renderV2()}
          </div>
        </Grid>
      </Grid>
    </div>
  );
};
