import { Grid, makeStyles, Button, Box } from "@material-ui/core";
import { useSnackbar } from "notistack";
import { useEffect, useState } from "react";
import {
  ActionSection,
  GallerySection,
  OverviewSection,
  RefundSection,
  StatusSection,
} from "./components";
import { BigNumber } from "ethers";
import { useConnectedWeb3Context } from "contexts";
import { ZERO } from "utils/number";
import { IIDODetails, IMetadata } from "types";
import { useIsMountedRef } from "helpers";
import { IDOService } from "services/ido";
import { IDOv2Service } from "services/idov2";
import { DefaultReadonlyProvider, getTierFactor } from "config/networks";
import { ERC20Service } from "services/erc20";
import { NULL_ADDRESS, REFUND_CONTRACTS } from "config/constants";
import { calculateGasMargin } from "utils";
import { IDOv3Service } from "services/idov3";
import { TierService } from "services/tier";
import { IDOv4Service } from "services/idov4";
import { IDOVersion } from "utils/enums";
import { IDOSecService } from "services/idoSec";
import { useHistory } from "react-router-dom";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";

const useStyles = makeStyles((theme) => ({
  content: { padding: "32px 16px" },
  mainContainer: {
    marginTop: 16,
    marginBottom: 16,
  },
}));

interface IState {
  personalTotalFund: BigNumber;
  personalReleased: BigNumber;
  currentRound: BigNumber;
  roundsTotalAllocation: { [key: string]: BigNumber };
  personalRoundMaxAllocation: BigNumber;
  personalRoundFunded: BigNumber;
  personalReleasableAmount: BigNumber;
  tierWhitelisted: boolean;
  whitelistEnabled: boolean;
  tierId: number;
}

interface IProps {
  data: IIDODetails & { meta: IMetadata };
  onReloadIDO: () => Promise<void>;
}

export const IDODetailsContent = (props: IProps) => {
  const {
    account,
    library: provider,
    networkId,
    setTxModalInfo,
    setWalletConnectModalOpened,
  } = useConnectedWeb3Context();
  const [state, setState] = useState<IState>({
    personalTotalFund: ZERO,
    personalReleased: ZERO,
    personalRoundMaxAllocation: ZERO,
    personalRoundFunded: ZERO,
    currentRound: ZERO,
    roundsTotalAllocation: {},
    personalReleasableAmount: ZERO,
    tierWhitelisted: false,
    whitelistEnabled: true,
    tierId: 0,
  });
  const isMountedRef = useIsMountedRef();
  const classes = useStyles();
  const { data } = props;
  const { enqueueSnackbar } = useSnackbar();
  let history = useHistory();
  const handleClick = () => history.push("/idos");
  const refundContract = REFUND_CONTRACTS[data.address.toLowerCase()] || "";

  const loadPublicInfo = async () => {
    if (data.version === IDOVersion.v1) {
      return;
    }
    if (data.version === IDOVersion.v2) {
      try {
        const ido = new IDOv2Service(
          provider || DefaultReadonlyProvider,
          account,
          data.address
        );
        const rounds = [1, 2, 3, 4, 5, 6];
        const currentRound = await ido.getCurrentRoundId();
        const roundsMaxAllocations = await Promise.all(
          rounds.map((roundId) =>
            ido.getRoundTotalAllocation(BigNumber.from(roundId))
          )
        );
        if (isMountedRef.current === true) {
          const roundsTotalAllocation: { [key: string]: BigNumber } = {};
          rounds.forEach((round, index) => {
            roundsTotalAllocation[round] = roundsMaxAllocations[index];
          });
          setState((prev) => ({
            ...prev,
            roundsTotalAllocation,
            currentRound,
          }));
        }
      } catch (error) {
        if (isMountedRef.current === true) {
          setState((prev) => ({
            ...prev,
            roundsTotalAllocation: {},
            currentRound: ZERO,
          }));
        }
      }
    } else if (data.version === IDOVersion.sec) {
      const ido = new IDOSecService(
        provider || DefaultReadonlyProvider,
        account,
        data.address
      );
      const rounds = [1, 2];
      const currentRound = await ido.getCurrentRoundId();
      const roundsMaxAllocations = await Promise.all(
        rounds.map((roundId) =>
          ido.getRoundTotalAllocation(BigNumber.from(roundId))
        )
      );
      if (isMountedRef.current === true) {
        const roundsTotalAllocation: { [key: string]: BigNumber } = {};
        rounds.forEach((round, index) => {
          roundsTotalAllocation[round] = roundsMaxAllocations[index];
        });
        setState((prev) => ({
          ...prev,
          roundsTotalAllocation,
          currentRound,
          whitelistEnabled: true,
        }));
      }
    } else {
      try {
        const ido = new IDOv3Service(
          provider || DefaultReadonlyProvider,
          account,
          data.address
        );
        const rounds = [1, 2, 3, 4, 5, 6];
        const currentRound = await ido.getCurrentRoundId();
        const roundsMaxAllocations = await Promise.all(
          rounds.map((roundId) =>
            ido.getRoundTotalAllocation(BigNumber.from(roundId))
          )
        );
        const whitelistEnabled = await ido.whitelistEnabled();
        if (isMountedRef.current === true) {
          const roundsTotalAllocation: { [key: string]: BigNumber } = {};
          rounds.forEach((round, index) => {
            roundsTotalAllocation[round] = roundsMaxAllocations[index];
          });
          setState((prev) => ({
            ...prev,
            roundsTotalAllocation,
            currentRound,
            whitelistEnabled,
          }));
        }
      } catch (error) {
        if (isMountedRef.current === true) {
          setState((prev) => ({
            ...prev,
            roundsTotalAllocation: {},
            currentRound: ZERO,
            whitelistEnabled: true,
          }));
        }
      }
    }
  };

  const loadPersonalRoundInfo = async () => {
    if (
      state.currentRound.isZero() ||
      !account ||
      !provider ||
      data.version === IDOVersion.v1
    ) {
      setState((prev) => ({
        ...prev,
        personalRoundFunded: ZERO,
        personalRoundMaxAllocation: ZERO,
      }));
      return;
    }

    try {
      if (data.version === IDOVersion.sec) {
        const ido = new IDOSecService(provider, account, data.address);
        const personalRoundMaxAllocation = await ido.getMaxAllocation(account);

        let personalRoundFunded = ZERO;
        if (state.currentRound.toNumber() === 1) {
          personalRoundFunded = (await ido.whitelistFunders(account))
            .totalFunded;
        } else if (state.currentRound.toNumber() === 2) {
          personalRoundFunded = (await ido.fcfsFunders(account)).totalFunded;
        }

        if (isMountedRef.current === true) {
          setState((prev) => ({
            ...prev,
            personalRoundFunded,
            personalRoundMaxAllocation,
          }));
        }
        return;
      }
      const ido =
        data.version === IDOVersion.v2
          ? new IDOv2Service(provider, account, data.address)
          : new IDOv3Service(provider, account, data.address);
      const personalRoundMaxAllocation = await ido.getMaxAllocation(account);

      let personalRoundFunded = ZERO;
      if (state.currentRound.toNumber() < 5) {
        personalRoundFunded = (await ido.tierFunders(account)).totalFunded;
      } else if (state.currentRound.toNumber() === 5) {
        personalRoundFunded = (await ido.whitelistFunders(account)).totalFunded;
      } else if (state.currentRound.toNumber() === 6) {
        personalRoundFunded = (await ido.fcfsFunders(account)).totalFunded;
      }

      if (isMountedRef.current === true) {
        setState((prev) => ({
          ...prev,
          personalRoundFunded,
          personalRoundMaxAllocation,
        }));
      }
    } catch (error) {
      console.error(error);
      if (isMountedRef.current === true) {
        setState((prev) => ({
          ...prev,
          personalRoundFunded: ZERO,
          personalRoundMaxAllocation: ZERO,
        }));
      }
    }
  };

  const loadPersonalInfo = async () => {
    if (!account || !provider) {
      setState((prev) => ({
        ...prev,
        personalTotalFund: ZERO,
        personalReleased: ZERO,
        personalRoundMaxAllocation: ZERO,
        personalRoundFunded: ZERO,
        personalReleasableAmount: ZERO,
      }));
      return;
    }
    try {
      if (data.version === IDOVersion.v1) {
        const ido = new IDOService(provider, account, data.address);
        const [funder, releasable] = await Promise.all([
          ido.getFunderInfo(account),
          ido.getClaimableAmount(account),
        ]);

        if (isMountedRef.current === true)
          setState((prev) => ({
            ...prev,
            personalTotalFund: funder.totalFunded,
            personalReleased: funder.release,
            personalReleasableAmount: releasable,
          }));
      } else if (data.version === IDOVersion.v2) {
        const ido = new IDOv2Service(provider, account, data.address);
        const funder = await ido.getFunderInfo(account);
        const releasable = await ido.getClaimableAmount(account);
        if (isMountedRef.current === true)
          setState((prev) => ({
            ...prev,
            personalTotalFund: funder.totalFunded,
            personalReleased: funder.release,
            personalReleasableAmount: releasable,
          }));
      } else if (data.version === IDOVersion.sec) {
        const ido = new IDOSecService(provider, account, data.address);
        const funder = await ido.getFunderInfo(account);
        const releasable = await ido.getClaimableAmount(account);

        if (isMountedRef.current === true)
          setState((prev) => ({
            ...prev,
            personalTotalFund: funder.totalFunded,
            personalReleased: funder.release,
            personalReleasableAmount: releasable,
          }));
      } else {
        // v3, v4
        const ido = new IDOv3Service(provider, account, data.address);
        const funder = await ido.getFunderInfo(account);
        const releasable = await ido.getClaimableAmount(account);
        const tierWhitelisted = await ido.isTierWhitelisted(account);

        const tier = new TierService(
          provider,
          account,
          getTierFactor(networkId).tier
        );
        const tierId = await tier.getTierId(account);

        if (isMountedRef.current === true)
          setState((prev) => ({
            ...prev,
            personalTotalFund: funder.totalFunded,
            personalReleased: funder.release,
            personalReleasableAmount: releasable,
            tierWhitelisted,
            tierId,
          }));
      }
    } catch (error) {
      if (isMountedRef.current === true)
        setState((prev) => ({
          ...prev,
          personalTotalFund: ZERO,
          personalReleased: ZERO,
          personalReleasableAmount: ZERO,
          tierWhitelisted: false,
        }));
    }
  };

  useEffect(() => {
    loadPublicInfo();
    const interval = setInterval(loadPublicInfo, 60000);

    return () => {
      clearInterval(interval);
    };
  }, [data.address]);

  useEffect(() => {
    loadPersonalInfo();
    const interval = setInterval(loadPersonalInfo, 60000);

    return () => {
      clearInterval(interval);
    };
  }, [data.address, account]);

  useEffect(() => {
    loadPersonalRoundInfo();
    const interval = setInterval(loadPersonalRoundInfo, 60000);

    return () => {
      clearInterval(interval);
    };
  }, [data.address, account, state.currentRound._hex]);

  const onBuy = async (amount: BigNumber) => {
    if (!account || !provider) {
      setWalletConnectModalOpened(true);
      return;
    }
    try {
      if (data.fundToken !== NULL_ADDRESS) {
        const fundToken = new ERC20Service(provider, account, data.fundToken);
        setTxModalInfo(true, "Checking allowance...");
        const hasEnoughAllowance = await fundToken.hasEnoughAllowance(
          account,
          data.address,
          amount
        );

        if (!hasEnoughAllowance) {
          setTxModalInfo(true, "Approving...");
          const tx = await fundToken.approveUnlimited(data.address);
          setTxModalInfo(
            true,
            "Approving...",
            "Please wait until tx is confirmed",
            tx
          );
          await provider.waitForTransaction(tx);
        }
      }

      const ido = new IDOv4Service(provider, account, data.address);
      setTxModalInfo(true, "Buying...");
      const gasPrice = await provider.getGasPrice();

      if (data.fundToken === NULL_ADDRESS) {
        let estimatedGas = BigNumber.from("350000");

        const tx = await ido.buyWithBNB(
          amount,
          gasPrice,
          calculateGasMargin(estimatedGas)
        );
        setTxModalInfo(
          true,
          "Buying...",
          "Please wait until tx is confirmed",
          tx
        );
        await provider.waitForTransaction(tx);
      } else {
        let estimatedGas = BigNumber.from("380000");

        const tx = await ido.buy(
          amount,
          gasPrice,
          calculateGasMargin(estimatedGas)
        );
        setTxModalInfo(
          true,
          "Buying...",
          "Please wait until tx is confirmed",
          tx
        );
        const response = await provider.waitForTransaction(tx);
        if (response.status === 0) {
          enqueueSnackbar("Failed to buy");
        } else {
          enqueueSnackbar("You've bought successfully");
        }
      }

      setTxModalInfo(true, "Reloading ...");
      await props.onReloadIDO();
      await loadPublicInfo();
      await loadPersonalInfo();
      await loadPersonalRoundInfo();

      setTxModalInfo(false);
    } catch (error: any) {
      enqueueSnackbar(error.message || "Something went wrong", {
        variant: "error",
      });
      setTxModalInfo(false);
    }
  };

  const onClaim = async () => {
    if (!account || !provider) {
      setWalletConnectModalOpened(true);
      return;
    }
    try {
      setTxModalInfo(true, "Claiming ...");
      const gasPrice = await provider.getGasPrice();

      if (data.version === IDOVersion.v1) {
        const ido = new IDOService(provider, account, data.address);
        const estimatedGas = await ido.claimEstimateGas();
        const tx = await ido.claim(gasPrice, calculateGasMargin(estimatedGas));
        setTxModalInfo(
          true,
          "Claiming...",
          "Please wait until tx is confirmed",
          tx
        );
        await provider.waitForTransaction(tx);
      } else if (data.version === IDOVersion.v2) {
        const ido = new IDOv2Service(provider, account, data.address);

        let estimatedGas = BigNumber.from("300000");
        const tx = await ido.claim(gasPrice, calculateGasMargin(estimatedGas));
        setTxModalInfo(
          true,
          "Claiming...",
          "Please wait until tx is confirmed",
          tx
        );
        await provider.waitForTransaction(tx);
      } else if (data.version === IDOVersion.sec) {
        const ido = new IDOv3Service(provider, account, data.address);

        let estimatedGas = BigNumber.from("300000");
        try {
          estimatedGas = await ido.estimateGas("claim", []);
        } catch (error) {}
        const tx = await ido.claim(gasPrice, calculateGasMargin(estimatedGas));
        setTxModalInfo(
          true,
          "Claiming...",
          "Please wait until tx is confirmed",
          tx
        );
        await provider.waitForTransaction(tx);
      } else {
        // v3, v4
        const ido = new IDOv3Service(provider, account, data.address);

        let estimatedGas = BigNumber.from("300000");
        try {
          estimatedGas = await ido.estimateGas("claim", []);
        } catch (error) {}
        const tx = await ido.claim(gasPrice, calculateGasMargin(estimatedGas));
        setTxModalInfo(
          true,
          "Claiming...",
          "Please wait until tx is confirmed",
          tx
        );
        await provider.waitForTransaction(tx);
      }
      enqueueSnackbar("Claimed successfully");
      await loadPersonalInfo();
      setTxModalInfo(false);
    } catch (error: any) {
      enqueueSnackbar(error.message || "Something went wrong", {
        variant: "error",
      });
      setTxModalInfo(false);
    }
  };
  return (
    <div className={classes.content}>
      <Button color="primary" onClick={handleClick} variant="text">
        <ChevronLeftIcon fontSize="small" />
        Go back
      </Button>
      <Grid container spacing={2} className={classes.mainContainer}>
        <Grid item xs={12}>
          <OverviewSection data={data as any} />
        </Grid>
      </Grid>
      <Grid container spacing={2} className={classes.mainContainer}>
        <Grid item xs={12} md={5}>
          <ActionSection
            data={data as any}
            onBuy={onBuy}
            onClaim={onClaim}
            {...state}
          />
          {refundContract !== "" && <RefundSection address={refundContract} />}
        </Grid>
        <Grid item sm={12} md={7}>
          <StatusSection data={data as any} {...state} />
        </Grid>
      </Grid>
      <Grid container spacing={2} className={classes.mainContainer}>
        {data.meta.images && data.meta.images.length > 0 && (
          <Grid item sm={12}>
            <GallerySection data={data as any} />
          </Grid>
        )}
      </Grid>
    </div>
  );
};
