import { BigNumber, Contract, Wallet, ethers } from "ethers";
import { IFunder, Maybe } from "types";
import idoAbi from "abis/idov4.json";

class IDOv4Service {
  provider: any;
  contract: Contract;

  constructor(provider: any, signerAddress: Maybe<string>, addr: string) {
    this.provider = provider;
    if (signerAddress) {
      const signer: Wallet = provider.getSigner();
      this.contract = new ethers.Contract(addr, idoAbi, provider).connect(
        signer
      );
    } else {
      this.contract = new ethers.Contract(addr, idoAbi, provider);
    }
  }

  get address(): string {
    return this.contract.address;
  }

  async getMaxAllocation(address: string) {
    return this.contract.getMaxAllocation(address);
  }

  async getClaimableAmount(address: string) {
    return this.contract.getClaimableAmount(address);
  }

  async isTierWhitelisted(address: string) {
    return this.contract.isTierWhitelisted(address);
  }

  async whitelistEnabled() {
    return this.contract.whitelistEnabled();
  }

  async getFunderInfo(address: string): Promise<IFunder> {
    const funder = await this.contract.getFunderInfo(address);
    return {
      totalFunded: funder[0],
      release: funder[1],
    };
  }

  async tierFunders(address: string): Promise<IFunder> {
    const funder = await this.contract.tierFunders(address);
    return {
      totalFunded: funder[0],

      release: funder[1],
    };
  }

  async whitelistFunders(address: string): Promise<IFunder> {
    const funder = await this.contract.whitelistFunders(address);
    return {
      totalFunded: funder[0],

      release: funder[1],
    };
  }

  async fcfsFunders(address: string): Promise<IFunder> {
    const funder = await this.contract.fcfsFunders(address);
    return {
      totalFunded: funder[0],
      release: funder[1],
    };
  }

  async getRoundTotalAllocation(roundId: BigNumber) {
    return this.contract.getRoundTotalAllocation(roundId);
  }

  async getCurrentRoundId() {
    return this.contract.getCurrentRoundId();
  }

  async buyWithBNB(
    amount: BigNumber,
    gasPrice: BigNumber,
    gasLimit: BigNumber
  ) {
    const transactionObject = await this.contract.buyWithBNB({
      value: amount,
      gasPrice,
      gasLimit,
    });
    console.log(`buyWithBNB transaction hash: ${transactionObject.hash}`);
    return transactionObject.hash;
  }

  async buy(amount: BigNumber, gasPrice: BigNumber, gasLimit: BigNumber) {
    const transactionObject = await this.contract.buy(amount, {
      gasPrice,
      gasLimit,
    });
    console.log(`buy transaction hash: ${transactionObject.hash}`);
    return transactionObject.hash;
  }

  async estimateGas(func: string, args: any[], value?: BigNumber) {
    return this.contract.estimateGas[func](...args, { value: value || "0x0" });
  }

  async claim(gasPrice: BigNumber, gasLimit: BigNumber) {
    const transactionObject = await this.contract.claim({
      gasPrice,
      gasLimit,
    });
    console.log(`Claim transaction hash: ${transactionObject.hash}`);
    return transactionObject.hash;
  }
}

export { IDOv4Service };
