import { Contract, Wallet, ethers } from "ethers";
import { Maybe } from "types";
import idoRefundAbi from "abis/idoRefund.json";

class IDORefundService {
  provider: any;
  contract: Contract;

  constructor(provider: any, signerAddress: Maybe<string>, addr: string) {
    this.provider = provider;
    if (signerAddress) {
      const signer: Wallet = provider.getSigner();
      this.contract = new ethers.Contract(addr, idoRefundAbi, provider).connect(
        signer
      );
    } else {
      this.contract = new ethers.Contract(addr, idoRefundAbi, provider);
    }
  }

  get address(): string {
    return this.contract.address;
  }

  async refund() {
    const transactionObject = await this.contract.refund();
    console.log(`refund transaction hash: ${transactionObject.hash}`);
    return transactionObject.hash;
  }
}

export { IDORefundService };
