import { Grid, makeStyles } from "@material-ui/core";
import { PageWrapper, SimpleLoader } from "components";
import { IDOS_TO_HIDE } from "config/constants";
import { useIDO } from "helpers";
import { useSnackbar } from "notistack";
import { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import { isAddress } from "utils/tools";
import { IDODetailsContent } from "./IDODetailsContent";

const useStyles = makeStyles((theme) => ({
  content: { paddingBottom: 32, paddingTop: 32 },
}));

const IDODetailsPage = () => {
  const params = useParams();
  const history = useHistory();
  const id = (params as any).id;
  const { enqueueSnackbar } = useSnackbar();
  const { data, loading, load } = useIDO(id);

  useEffect(() => {
    if (
      !id ||
      !isAddress(id) ||
      IDOS_TO_HIDE.includes((id || "").toLowerCase())
    ) {
      enqueueSnackbar("Invalid pad", { variant: "error" });
      history.push("/idos");
    }
  }, [id]);

  useEffect(() => {
    if (!loading && !data) {
      history.push("/idos");
    }
  }, [loading, data]);

  const renderContent = () => {
    if (!data || !data.meta) return null;
    return <IDODetailsContent data={data as any} onReloadIDO={load} />;
  };

  return (
    <PageWrapper>
      {!data || !data.meta ? <SimpleLoader /> : renderContent()}
    </PageWrapper>
  );
};

export default IDODetailsPage;
