import { makeStyles, Typography } from "@material-ui/core";
import React, { useState } from "react";
import { IIDO, IMetadata } from "types";
import PanoramaIcon from "@material-ui/icons/Panorama";
import { LazyImage } from "components";
import { transparentize } from "polished";

const IMAGE_HEIGHT = 150;

const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: 16,
  },
  header: {
    display: "flex",
    alignItems: "center",
    color: theme.colors.primary,
  },
  title: {
    color: theme.colors.neutralLighter,
    fontSize: 24,
    fontWeight: 600,
    marginLeft: 12,
  },
  content: {
    marginTop: 12,
    display: "flex",
    // flexWrap: "wrap",
    margin: "0 -6px",
    overflowX: "auto",
    paddingBottom: 16,
  },
  imgWrapper: {
    margin: 6,
    height: IMAGE_HEIGHT,
    // minWidth: IMAGE_HEIGHT,
  },
  img: {
    height: IMAGE_HEIGHT,

    border: `1px solid ${transparentize(0.8, theme.colors.neutralLighter)}`,
  },
}));

interface IProps {
  data: IIDO & { meta: Required<IMetadata> };
}

interface IState {
  imgWidths: { [key: string]: number };
}

export const GallerySection = (props: IProps) => {
  const classes = useStyles();
  const [state, setState] = useState<IState>({ imgWidths: {} });

  return (
    <div className={classes.root}>
      <div className={classes.header}>
        <PanoramaIcon />
        <Typography className={classes.title}>Photos</Typography>
      </div>
      <div className={classes.content}>
        {props.data.meta.images.map((image) => {
          return (
            <LazyImage
              key={image}
              src={image}
              className={classes.imgWrapper}
              imgClassName={classes.img}
              onImgLoaded={(width, height) => {
                setState((prev) => ({
                  ...prev,
                  imgWidths: {
                    ...prev.imgWidths,
                    [image]: (IMAGE_HEIGHT * width) / height,
                  },
                }));
              }}
              imgStyle={
                state.imgWidths[image] ? { width: state.imgWidths[image] } : {}
              }
            />
          );
        })}
      </div>
    </div>
  );
};
