import { Grid, makeStyles, Typography } from "@material-ui/core";
import { IDOMetaDataRow, LazyImage } from "components";
import React from "react";
import { IIDO, IMetadata } from "types";
import DeveloperModeIcon from "@material-ui/icons/DeveloperMode";
import ContentCopyIcon from "@material-ui/icons/FileCopy";
import copy from "copy-to-clipboard";
import { useConnectedWeb3Context } from "contexts";
import { getEtherscanUri } from "config/networks";
import { shortenAddress } from "utils";
import { transparentize } from "polished";
import { useSnackbar } from "notistack";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    alignItems: "flex-start",
    padding: 16,
    [theme.breakpoints.down("xs")]: {
      flexDirection: "column",
    },
    [theme.breakpoints.down("sm")]: {
      padding: 0,
    },
    backgroundColor: theme.colors.neutralLight,
  },
  content: { padding: 16, flex: 1 },
  logoWrapper: {
    marginRight: 16,
    // backgroundColor: theme.colors.primary,
    borderRadius: "50%",
    [theme.breakpoints.down("xs")]: {
      alignSelf: "center",
    },
  },
  logo: {
    borderRadius: "50%",
    width: 60,
    height: 60,
  },
  title: {
    color: theme.colors.neutralLighter,
    fontSize: 24,
    fontWeight: 600,
  },

  header: {
    display: "flex",
    alignItems: "flex-start",
    marginBottom: 16,
  },
  symbol: {
    color: theme.colors.neutralDark,
    fontSize: 14,
    backgroundColor: transparentize(0.2, theme.colors.neutralLighter),
    padding: "4px 10px",

    marginTop: 12,
    fontWeight: 700,
    letterSpacing: 1,
  },
  description: { color: theme.colors.neutralLighter, fontSize: 14 },
  bottom: {
    display: "flex",
    justifyContent: "space-between",
    paddingTop: 16,
    flexDirection: "column",
  },
  contractWrapper: {
    display: "flex",
    alignItems: "center",
    marginTop: 16,
    "& div": {
      color: theme.colors.primary,
      padding: 2,

      backgroundColor: transparentize(0.8, theme.colors.primary),
      "& svg": {
        height: 18,
        width: 18,
      },
    },
    "& span": {
      color: theme.colors.neutralLighter,
    },
    "& a": { color: theme.colors.primary, padding: 2, textDecoration: "none" },
  },
  copy: {
    cursor: "pointer",
    transition: "all 0.4s",
    "&:hover": {
      opacity: 0.7,
    },
    "& svg": {
      height: 18,
      width: 18,
    },
  },
  meta: {
    marginBottom: -12,
    [theme.breakpoints.down("sm")]: {
      marginBottom: 0,
    },
  },
  warn: {
    color: "#a00",
    marginTop: 4,
  },
}));

interface IProps {
  data: IIDO & { meta: Required<IMetadata> };
}

export const OverviewSection = (props: IProps) => {
  const {
    data: {
      address,
      meta: { logo, title, tokenSymbol, description },
    },
  } = props;

  const classes = useStyles();
  const { networkId } = useConnectedWeb3Context();
  const etherscanUri = getEtherscanUri(networkId);
  const { enqueueSnackbar } = useSnackbar();

  const onCopy = () => {
    copy(address);
    enqueueSnackbar("Copied to clipboard");
  };

  return (
    <div className={classes.root}>
      <div className={classes.content}>
        <div className={classes.header}>
          <LazyImage
            src={logo}
            className={classes.logoWrapper}
            imgClassName={classes.logo}
          />
          <Typography className={classes.title}>
            {title} <span className={classes.symbol}>{tokenSymbol}</span>
          </Typography>
        </div>
        <Typography className={classes.description}>{description}</Typography>
        <div className={classes.bottom}>
          <IDOMetaDataRow data={props.data.meta} className={classes.meta} />
          <div>
            <div className={classes.contractWrapper}>
              <div>
                <DeveloperModeIcon />
              </div>
              &nbsp;
              <span>Contract:</span>&nbsp;
              <a
                href={`${etherscanUri}address/${address}`}
                target="_blank"
                rel="noreferrer"
              >
                {shortenAddress(address)}
              </a>
              &nbsp;
              <span className={classes.copy} onClick={onCopy}>
                <ContentCopyIcon />
              </span>
            </div>
            <Typography className={classes.warn}>
              Don't send money to this address
            </Typography>
          </div>
        </div>
      </div>
    </div>
  );
};
